<script>
// import MyTable from './components/table/MyTable.vue'
import getUer from './components/getUser.vue'

export default {
  name:'App',
  components:{
    getUer
    // MyTable
  },
  data(){
    return{
      tablelist:[]
    }
  },
  created(){
    this.getlist()
  },
  methods: {
    async getlist(){
      this.$http.post('/qury2')
          .then((res) => {
            // console.log(JSON.stringify(res))
            this.tablelist = res["data"]
          })
          .catch(function (error) {
            console.log(error);
          });
    },
    // 删除功能
    onRemove(id){
      this.tablelist = this.tablelist.filter(x=>x.id !== id)
    },
    onInputConfirm(row){
      // 1. 把用户在文本框中输入的值，预先转存到常量 val 中
      const vla = row.inputValue
      // 2. 清空文本框的值
      row.inputValue = ''
      // 3. 隐藏文本框
      row.inputVisible = false
      // 1.1 判断 val 的值是否为空，如果为空，则不进行添加
      // 1.2 判断 val 的值是否已存在于 tags 数组中，防止重复添加
      if(!vla || row.tags.indexOf(vla) !== -1) return
      // 2. 将用户输入的内容，作为新标签 push 到当前行的 tags 数组中
      row.tags.push(vla)
    }
  },
  directives:{
    focus(el){
      el.focus()
    }
  }
}
</script>

